export default {
	data() {
		return {
			base: {
				avatar: this.$config.avatar,
				CustomBar: this.CustomBar,
				title: this.$config.title
			},
			userInfo: this.$common.userInfo()
		}
	},
	created() {
		uni.$on('refreshUser', (data) => {
			this.userInfo = data
		})
	},
	methods: {
		logout() {
			uni.clearStorageSync()
			this.$common.errorToShow('已安全退出');
			this.$api.logout();
			uni.reLaunch({
				url: '/pages/user/login'
			})
		},
		navigateTo(url) {
			uni.navigateTo({
				url: url
			})
		}
	}
}
